import React, { useState, useEffect } from "react";
import { Items } from "./Items";
import { TikTokEmbed } from 'react-social-media-embed';

const DisplayWindow = () => {
  const [currentMedia, setCurrentMedia] = useState();
  const [desc, setDesc] = useState("");
  const [showStartDisplay, setShowStartDisplay] = useState(true);
  const [seenMedia, setSeenMedia] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  const displayNext = () => {

    if (isLoading === true) {
      return;
    }

    setShowStartDisplay(false);
    const numItems = Object.keys(new Items().tiktoks).length + Object.keys(new Items().quotes).length + Object.keys(new Items().songs).length;
    if (seenMedia.length === numItems) {
      setCurrentMedia("You've seen everything! Refresh the page to start over.");
      setDesc('');
      return;
    }

    let nextMedia = getNextDisplayItem();

    while (seenMedia.includes(nextMedia[1])) {
      nextMedia = getNextDisplayItem();
    }

    setSeenMedia([...seenMedia, nextMedia[1]]);
    setIsLoading(true);
    setCurrentMedia(nextMedia[0][0]);
    setTimeout( () => {
      setIsLoading(false);
    }, 1000);


    let newDesc = nextMedia[0][1];

    if (newDesc !== 'description' && newDesc !== '') {
      setDesc(nextMedia[0][1]);
    } else {
      setDesc('');
    }
  };

  // quotes
  // tiktoks
  // music
  const getNextDisplayItem = () => {
    const items = new Items();
    const mediaTypes = ["tiktoks", "songs", "quotes"];
    const randomMediaType = items.media[mediaTypes[Math.floor(Math.random() * mediaTypes.length)]];
    const randomKey = Object.keys(randomMediaType)[Math.floor(Math.random() * Object.keys(randomMediaType).length)];
    const randomMedia = randomMediaType[randomKey];
    return [randomMedia, randomKey];
  };

  const startDisplay = () => {
    if (showStartDisplay === false) {
      return;
    }

    return (
      <div>
        <p>
          This collection of media argues that being queer (focusing on gay men) creates a life experince that
          is detatched from the heteronormative life cycle. This collection of media also argues that
          being queer creates freedom (queer joy) and a unique way of living life but can also be
          an isolating experience. If you don't watch / lsiten to everything that is fine but I would suggest reading
          the descriptions for each piece of media since occasionally they reference material from class.
        </p>
        <p className="invert fancy"  onClick={displayNext}>Tap here to start exploring</p>
      </div>
    )
  }

  const changeVisibility = () => {
    setIsLoading(!isLoading);
  }

  const mediaDisplay = () => {
    return (
      <div>
        <div className="currentMedia">
        {!isLoading && currentMedia}
        </div>
        <p>{desc}</p>
        <p className='nextButton' onClick={displayNext}>Next</p>
      </div>
    )
  }

  const display = () => {
    return (
      <div className="display">
        {startDisplay() ? startDisplay() : mediaDisplay()}
      </div>
    );
  };

  return (
    <div>
        {display()}
    </div>
  );
};

export default DisplayWindow;
